function makeNodeComponent(nodeName) {
  const func = function (props) {
    const { variants, args, overrides } = deriveRenderOpts(props, {
      name: nodeName,
      descendantNames: [...PlasmicDescendants[nodeName]],
      internalArgPropNames: PlasmicMobiCycle__ArgProps,
      internalVariantPropNames: PlasmicMobiCycle__VariantProps
    });

    return PlasmicMobiCycle__RenderFunc({
      variants,
      args,
      overrides,
      forNode: nodeName
    });
  };
  if (nodeName === "root") {
    func.displayName = "PlasmicMobiCycle";
  } else {
    func.displayName = `PlasmicMobiCycle.${nodeName}`;
  }
  return func;
}

